@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: radial-gradient(circle, hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  font-family: 'Barlow Semi Condensed', sans-serif;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: #fff;

}
button {
  font-family: 'Barlow Semi Condensed', sans-serif;
}


.triangle-up {
  display: -webkit-flex;
  display: flex;
}
.triangle-down {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.image {
  background-color: #fff;
  margin: 15px;
}
.image > img {
  height: 56px;
}
.start-image {
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
  display: -webkit-flex;
  display: flex;
  
  cursor: pointer;
  -webkit-align-self: center;
          align-self: center;
  position: relative;
}
.start-image:hover {
  /*transform: scale(1.1);*/
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
}
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale();
            transform: scale();
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  }
  70% {
    -webkit-transform: scale();
            transform: scale();
    box-shadow: 0 0 0 50px rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-transform: scale();
            transform: scale();
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}
@keyframes pulse {
  0% {
    -webkit-transform: scale();
            transform: scale();
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  }
  70% {
    -webkit-transform: scale();
            transform: scale();
    box-shadow: 0 0 0 50px rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-transform: scale();
            transform: scale();
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}
.start {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%)
}
.result {
  position: absolute;
  top: 52.1%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%)
}
.rules {
  color: #fff;
}
.paper{
  height: 50px;
  width:50px;
  border-radius: 50%;
  border-color: transparent;
  border: 15px solid #4664F4;
  background-size: 50%;
  padding: 20px;
}

.rock {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border-color: transparent;
  border: 15px solid #DB2E4D;
